<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step==1" @click="step++">Next</li>
      <li v-if="step==2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <ul class="nav nav-tabs" id="myTab" role="tablist">
  <li class="nav-item" role="presentation" @click="step = 0">
    <button class="nav-link active" id="home-tab" data-bs-toggle="tab" data-bs-target="#home-tab-pane" 
      type="button" role="tab" aria-controls="home-tab-pane" aria-selected="true">메인</button>
  </li>
  <li class="nav-item" role="presentation" @click="step = 1">
    <button class="nav-link" id="profile-tab" data-bs-toggle="tab" data-bs-target="#profile-tab-pane"
      type="button" role="tab" aria-controls="profile-tab-pane" aria-selected="false">엥?</button>
  </li>
  <li class="nav-item" role="presentation" @click="step = 2">
    <button class="nav-link" id="contact-tab" data-bs-toggle="tab" data-bs-target="#contact-tab-pane" 
      type="button" role="tab" aria-controls="contact-tab-pane" aria-selected="false">잉?</button>
  </li>
</ul>

  <Container @saveContent="content = $event" :filter="filter" :vuestaData="vuestaData" :step="step" :uploadUrl="uploadUrl"/>

  <div class="footer" v-if="step == 0">
    <ul class="footer-button-plus">
      <input type="file" accept="image/*" id="file" class="inputfile" @change="upload"/>
      <label for="file" class="input-plus">+</label>
    </ul>
 </div>

</template>

<script>
import Container from './components/Container';
import {mapState} from 'vuex';
// import vuestaData from './data/vuestaData.js';
// import axios from 'axios';

export default {
  name: 'App',
  data() {
    return{
      // vuestaData: vuestaData,
      // moreCount: 0,
      step: 0,
      uploadUrl: "",
      content: "",
      filter: "",
    }
  },
  components: {
    Container,
  },
  methods:{
    publish(){
      var myVuestaData = {    
        "name": "JH",
        "userImage": "https://placeimg.com/100/100/nature",
        "postImage": this.uploadUrl,
        "likes": 9543,
        "date": "April 3",
        "liked": false,
        "content": this.content,
        "filter": this.filter,
      };
      // this.vuestaData.unshift(myVuestaData); // 왼쪽의 array에 데이터 넣어줌
      this.$store.commit('changeVuestaData', myVuestaData);
      this.step = 0;

    }
    ,
  upload(e){
    let files = e.target.files;
    let url = URL.createObjectURL(files[0]);
    this.uploadUrl = url;
    this.step++;
  }
},
computed : {
  ...mapState(['vuestaData']),
  ...mapState({ newName :'vuestaData'}),
},
mounted(){
  this.emitter.on('fire',(data)=>{
    this.filter = data;
  })
}

}
</script>

<style>
