import { createStore } from 'vuex'
import vuestaData from './data/vuestaData.js';
import axios from 'axios';

const store = createStore({
    state() {
        return {
            vuestaData: vuestaData,
            moreCount: 0,
        }
    },
    mutations :{ // state 수정하는 방법 정의하는 곳
        changeLikes(state, id){
            if(!state.vuestaData[id].liked){
                state.vuestaData[id].likes += 1;
                state.vuestaData[id].liked = true;
            }else{
                state.vuestaData[id].likes -= 1;
                state.vuestaData[id].liked = false;
            }
        },
        changeVuestaData(state, payload){
            state.vuestaData.unshift(payload);
        },
        setMore(state, payload){
            state.vuestaData.push(payload);
            state.moreCount += 1;
        }
    },
    actions : { // ajax 요청하거나 , 시간이 오래 걸리는 곳.
        more(context){
            // console.log(this.state.moreCount);
            axios.get(`https://lee-jooho.github.io/vue/more${this.state.moreCount}.json`)
            .then((result)=>{
              context.commit('setMore', result.data);
            }).catch((err)=>{
              console.log(err);
            })
        },
    },


})

export default store;